
import React from 'react';
import './components.css'
import { useState, useEffect } from 'react';
import scoreService from '../services/scoreService'

const Form = (props) => {



    if(props.courseToShow.length === 0)
    {
        return null
    }
    else
    {
        const parTotal = props.courseToShow.holes.map(hole => hole.par)
        return(
            <div className="scoreForm">
                <form onSubmit={props.handleFormSubmit}>
                    Your name: <input value={props.newName} onChange={props.handleName} />
                    <table className="scoreTable">
                        <tbody>
                        <tr><th id="row-header">Hole</th>
                            {props.courseToShow.holes.map(hole =>  
                                <th key={hole.id}> {hole.id} </th>
                            )}
                            <th id="row-header">Total</th>
                        </tr>
                        <tr><td id="row-header">Par</td>
                            {props.courseToShow.holes.map(hole =>  
                                <td key={hole.id}> {hole.par} </td>
                            )}
                            <td id="row-header"> {parTotal.reduce((s, p) => s + p)} </td>
                        </tr>
                        <tr><td id="row-header">Result</td>
                            {props.courseToShow.holes.map((hole, i) =>  
                                <td key={hole.id}> <input name={i} onChange={props.handleScore} size="2"/> </td>
                            )}
                        </tr>
                        </tbody>
                    </table>
                    <button type="submit">Save</button>
                </form>
            </div>
        )
    }
}

const Notification = ({message}) => {
    if (message === null) {
        return null
    }
    return (
        <div className="error">
          {message}
        </div>
    )
}



const Scores = () => {
    const [courseToShow, setCourseToShow] = useState([])

    const [score, setScore] = useState({})
    const [results, setResults] = useState([])
    const [newName, setNewName] = useState('')
    const [message, setMessage] = useState(null)
    let total = 0

    useEffect(() => {
        scoreService
            .getAll()
            .then(initialScores => {
                setResults(initialScores)
            })
    }, [])
    const handleName = (event) => {
        setNewName(event.target.value)
    }

    const handleScore = (event) => {
        setScore({
            ...score,
            [event.target.name] : event.target.value
        })
    }
    const handleFormSubmit = (event) => {
        event.preventDefault()

        if(Object.keys(score).length === 18 && newName !== '')
        {
            for(let i = 0; i < Object.keys(score).length; i++)
            {
                total = total + parseInt(score[`${i}`])
            }
            setCourseToShow([])
            setScore({})

            const resultObject = {
                name: newName,
                course: courses[0].name,
                score: total 
            }

            scoreService
                .create(resultObject)
                .then(returnedObject => {
                    setResults(results.concat(returnedObject))
                    total = 0
                    setMessage('Score saved!')
                    setTimeout(() => {
                        setMessage(null)
                    }, 5000)
                })
        }
        else if(Object.keys(score).length === 20 && newName !== '')
        {
            for(let i = 0; i < Object.keys(score).length; i++)
            {
                total = total + parseInt(score[`${i}`])
            }
            setCourseToShow([])
            setScore({})

            const resultObject = {
                name: newName,
                course: courses[1].name,
                score: total 
            }

            scoreService
                .create(resultObject)
                .then(returnedObject => {
                    setResults(results.concat(returnedObject))
                    total = 0
                    setNewName('')
                    setMessage('Score saved!')
                    setTimeout(() => {
                        setMessage(null)
                    }, 5000)
                })
        }
        else
        {
            setMessage('You must fill all inputs before saving')
            setTimeout(() => {
                setMessage(null)
            }, 5000)
        }
    }



    const courses = [
        {
            name: "Tampere Disc Golf Center (18 hole silver layout)",
            id: 1,
            holes: [
                {
                    id: 1,
                    par: 3
                },
                {
                    id: 2,
                    par: 3
                },
                {
                    id: 3,
                    par: 3
                },
                {
                    id: 4,
                    par: 3
                },
                {
                    id: 5,
                    par: 3
                },
                {
                    id: 6,
                    par: 4
                },
                {
                    id: 7,
                    par: 3
                },
                {
                    id: 8,
                    par: 3
                },
                {
                    id: 9,
                    par: 3
                },
                {
                    id: 10,
                    par: 4
                },
                {
                    id: 11,
                    par: 3
                },
                {
                    id: 12,
                    par: 3
                },
                {
                    id: 13,
                    par: 4
                },
                {
                    id: 14,
                    par: 4
                },
                {
                    id: 15,
                    par: 3
                },
                {
                    id: 16,
                    par: 4
                },
                {
                    id: 17,
                    par: 3
                },
                {
                    id: 18,
                    par: 3
                },
            ]
        },
        {
            name: "SahanmÃ¤ki DiscGolfPark (20 hole 2020 layout)",
            id: 2,
            holes: [
                {
                    id: 1,
                    par: 3
                },
                {
                    id: 2,
                    par: 3
                },
                {
                    id: 3,
                    par: 3
                },
                {
                    id: 4,
                    par: 4
                },
                {
                    id: 5,
                    par: 3
                },
                {
                    id: 6,
                    par: 3
                },
                {
                    id: 7,
                    par: 3
                },
                {
                    id: 8,
                    par: 3
                },
                {
                    id: 9,
                    par: 3
                },
                {
                    id: 10,
                    par: 3
                },
                {
                    id: 11,
                    par: 4
                },
                {
                    id: 12,
                    par: 3
                },
                {
                    id: 13,
                    par: 3
                },
                {
                    id: 14,
                    par: 3
                },
                {
                    id: 15,
                    par: 3
                },
                {
                    id: 16,
                    par: 4
                },
                {
                    id: 17,
                    par: 3
                },
                {
                    id: 18,
                    par: 3
                },
                {
                    id: 19,
                    par: 3
                },
                {
                    id: 20,
                    par: 3
                },
            ]
        }
    ]

    const handleCourseToShow = (id) => {
        setCourseToShow(courses.find(course => course.id === id))
    }

    return(
        <div>
            <header className="App-header">
                <h1>Here you can save your round score</h1>
            </header>
            <div>
                <Notification message={message} />
            </div>
            <div>
                <h2>Choose course</h2>
                {courses.map(course => <button key={course.id} onClick={() => handleCourseToShow(course.id)}> {course.name} </button>)}
            </div>
            <div>
                <Form courseToShow={courseToShow} newName={newName} handleName={handleName} handleScore={handleScore} handleFormSubmit={handleFormSubmit} />
            </div>
            <div>
                <h3>Player Scores: </h3>
                {results.map(result => <p key={result.id}>Player: {result.name}, Course: {result.course}, Score: {result.score} </p>)}
            </div>
            <div id="Footer">
                <p>Scores are saved to MongoDB Atlas</p>
            </div>
        </div>
    )

}

export default Scores;